import React from 'react'
import AssetAccordion from './AssetAccordion'
import { Row, Col, FormText } from 'reactstrap'
import 'swiper/css/swiper.css'
import '../../assets/scss/plugins/extensions/swiper.scss'
import img1 from '../../assets/img/slider/banner-31.jpg'
import img2 from '../../assets/img/slider/banner-22.jpg'
import img3 from '../../assets/img/slider/banner-23.jpg'
import img4 from '../../assets/img/slider/banner-24.jpg'
import img5 from '../../assets/img/slider/banner-25.jpg'
import Swiper from 'react-id-swiper'
import 'swiper/css/swiper.css'
import { getData } from '../../redux/actions/data-list/'
import { connect } from 'react-redux'
const params = {
  effect: 'cube',
  grabCursor: true,
  autoplay: {
    delay: 2000,
    disableOnInteraction: true
  },
  cubeEffect: {
    shadow: true,
    slideShadows: true,
    shadowOffset: 20,
    shadowScale: 0.94
  },
  pagination: {
    el: '.swiper-pagination'
  }
}

class Portfolio extends React.Component {
  state = {
    assetShow: false,
    liabilityShow: false,
    assetList: [],
    liabilityList: []
  }

  componentWillMount() {
    this.props.getData()
    this.getAssets()
  }

  getAssets = () => {
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    const { assetList, liabilityList } = this.state
    assetList.splice(0, assetList.length)
    liabilityList.splice(0, liabilityList.length)
    user?.assets?.length &&
      user.assets.forEach((e, i) => {
        let nomin = ''
        e?.nominees?.length &&
          e.nominees.forEach((n, idx) => {
            if (idx === e.nominees.length - 1) {
              nomin = nomin + n.name
            } else {
              nomin = nomin + n.name + ', '
            }
          })
        const cont = (
          <div>
            <Row>
              {e?.assetDetails?.map((x) => {
                if (x.val !== '') {
                  return (
                    <Col md="6" sm="12">
                      <FormText>
                        <b>{x.key} :</b>{' '}
                        {this.state.assetShow ? x.val : '***********'}
                      </FormText>
                    </Col>
                  )
                }
                return ''
              })}
              <Col md="6" sm="12">
                <FormText>
                  <b>Nominees :</b>{' '}
                  {this.state.assetShow ? nomin : '*******************'}
                </FormText>
              </Col>
            </Row>
          </div>
        )
        const ast = {
          id: i + 1,
          title: e.assetType,
          content: cont
        }
        assetList.push(ast)
      })
    user?.liabilities?.length &&
      user.liabilities.forEach((e, i) => {
        let nomin = ''
        e.nominees.forEach((n, idx) => {
          if (idx === e.nominees.length - 1) {
            nomin = nomin + n.name
          } else {
            nomin = nomin + n.name + ', '
          }
        })
        const cont = (
          <div>
            <Row>
              {e.liabilityDetails.map((x) => {
                if (x.val !== '') {
                  return (
                    <Col md="6" sm="12">
                      <FormText>
                        <b>{x.key} :</b>{' '}
                        {this.state.liabilityShow ? x.val : '***********'}
                      </FormText>
                    </Col>
                  )
                } else return ''
              })}
              <Col md="6" sm="12">
                <FormText>
                  <b>Nominees :</b>{' '}
                  {this.state.liabilityShow ? nomin : '*******************'}
                </FormText>
              </Col>
            </Row>
          </div>
        )
        const ast = {
          id: i + 1,
          title: e.liabilityType,
          content: cont
        }
        liabilityList.push(ast)
      })
    console.log(assetList)
    this.setState({
      assetList: assetList?.reverse(),
      liabilityList: liabilityList?.reverse()
    })
  }

  render() {
    return (
      <React.Fragment>
        <h2 className="warning spacing nodisplay">Portfolio</h2>
        <Row>
          <Col lg="8" md="6" sm="12">
            <Row className="match-height">
              <Col md="12">
                <AssetAccordion
                  heading="Assets List"
                  collapseItems={this.state.assetList}
                  show={() => {
                    this.setState({ assetShow: !this.state.assetShow })
                    this.getAssets()
                  }}
                  assetShow={this.state.assetShow}
                  page="portfolio"
                />
              </Col>
            </Row>
            <Row className="match-height">
              <Col md="12">
                <AssetAccordion
                  heading="Liabilities List"
                  collapseItems={this.state.liabilityList}
                  show={() => {
                    this.setState({ liabilityShow: !this.state.liabilityShow })
                    this.getAssets()
                  }}
                  assetShow={this.state.liabilityShow}
                  page="portfolio"
                />
              </Col>
            </Row>
          </Col>
          <Col lg="4" md="6" sm="12">
            <div className="swipermobile">
              <Swiper {...params}>
                <div
                  style={{
                    backgroundImage: `url(${img1})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img2})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img3})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img4})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img5})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
              </Swiper>
            </div>
          </Col>
        </Row>
      </React.Fragment>
    )
  }
}

export default connect(null, { getData })(Portfolio)
